body {
  background-color: #7895cb;
}

button {
  cursor: pointer;
  border: none;
  border-radius: 4px;
  box-shadow: 0 4px 4px #00000026;
}

.search-form {
  justify-content: center;
  gap: 10px;
  margin-top: 40px;
  display: flex;
}

.form-input {
  letter-spacing: .04em;
  color: #000;
  background-color: #c5dff8;
  border: 1px solid #0000;
  border-radius: 4px;
  outline: none;
  width: 264px;
  height: 40px;
  margin-right: 24px;
  padding: 8px 16px;
  font-size: 12px;
  line-height: 2;
  transition: border .25s cubic-bezier(.4, 0, .2, 1);
}

.form-input::placeholder {
  color: #000;
}

.form-input:focus, .form-input:hover {
  border: 1px solid #4a55a2;
}

.form-btn, .load-more {
  color: #fff;
  background-color: #4a55a2;
  border: 1px solid #0000;
  align-items: center;
  height: 40px;
  padding: 8px 24px;
  font-size: 16px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.form-btn:hover, .form-btn:focus, .load-more:hover, .load-more:focus {
  color: #000;
  background-color: #c5dff8;
}

.load-more {
  margin-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.gallery {
  flex-wrap: wrap;
  gap: 16px;
  margin-top: 40px;
  margin-bottom: 40px;
  padding: 15px;
  display: flex;
}

.gallery-item-wrap {
  border-radius: 4px;
  width: calc(25% - 12px);
  box-shadow: 0 1px 3px #0003, 0 1px 1px #00000024, 0 2px 1px -1px #0000001f;
}

.image {
  width: 100%;
  height: 210px;
  display: block;
}

.info {
  letter-spacing: .02em;
  text-align: center;
  background-color: #fff;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  flex-direction: row;
  margin: 0 auto;
  padding-left: 5px;
  padding-right: 5px;
  font-size: 14px;
  line-height: 1.5;
  display: flex;
}

.info-item {
  margin: 0;
  padding: 0;
  font-size: 16px;
}

